@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.l-inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .l-inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

.c-pagetop {
  position: fixed;
  right: 1.25rem;
  bottom: 2.5rem;
  text-indent: -9999;
  margin: 0;
}
@media screen and (min-width: 768px) {
  .c-pagetop {
    right: 1.875rem;
    bottom: 2rem;
  }
}

.c-pagetop a {
  position: relative;
  display: inline-block;
  width: 2.8125rem;
  height: 2.8125rem;
  background-color: #fff;
  color: #333;
  text-decoration: none;
  transition: all 0.3s;
}

.c-pagetop a::before {
  border-top: 2px solid #333;
  border-right: 2px solid #333;
  position: absolute;
  color: #fff;
  top: 1.125rem;
  right: 0.625rem;
  left: 0.625rem;
  width: 1.25rem;
  height: 1.25rem;
  margin: auto;
  content: "";
  transform: rotate(-45deg);
  transition: all 0.3s;
}

.c-pagetop a:hover {
  background-color: #333;
  color: #fff;
  transition: all 0.3s;
}

.c-pagetop a:hover::before {
  border-top: 2px solid #fff;
  border-right: 2px solid #fff;
}

.c-contact__button {
  width: 100%;
  text-align: center;
  margin-top: 3.0625rem;
}
@media screen and (min-width: 768px) {
  .c-contact__button {
    margin-top: 3.75rem;
  }
}

.c-button {
  position: relative;
  display: block;
  padding: 0.6875rem 0 0.625rem;
  margin: 0 auto;
  font-size: 1.25rem;
  letter-spacing: 0.2em;
  text-decoration: none;
  background: #333;
  color: #fff;
  border: 1px solid #fff;
  transition: color 0.3s ease;
}
@media screen and (min-width: 768px) {
  .c-button {
    width: 22.5rem;
    padding: 0.9375rem 6.125rem 1rem;
    letter-spacing: 0.15em;
  }
}
.c-button:hover {
  color: #333;
}
.c-button:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}

.c-button::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: #fff;
  transform: scaleX(0);
  transform-origin: right;
  transition: all 0.3s ease;
  transition-property: transform;
  z-index: -1;
}

.c-hamburger {
  display: block;
  position: absolute;
  width: 1.5rem;
  height: 0.8125rem;
  right: 1.375rem;
  z-index: 9998;
}
@media screen and (min-width: 768px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger span {
  display: block;
  position: relative;
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
  border-radius: 4px;
  transition: all 0.4s;
}

.c-hamburger span:nth-child(1) {
  top: 0;
}

.c-hamburger span:nth-child(2) {
  top: 0.4375rem;
}

.c-hamburger span:nth-child(3) {
  top: 0.875rem;
}

.c-hamburger.is-open span:nth-child(1) {
  top: 8px;
  transform: rotate(-45deg);
}

.c-hamburger.is-open span:nth-child(2) {
  opacity: 0;
}

.c-hamburger.is-open span:nth-child(3) {
  top: 5px;
  transform: rotate(45deg);
}

