.p-footer__inner {
  padding: 3rem 0;
}
@media screen and (min-width: 768px) {
  .p-footer__inner {
    padding: 2rem 2rem 2rem 1.625rem;
  }
}

.p-footer__content {
  display: block;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .p-footer__content {
    display: flex;
  }
}

.p-footer__logo {
  width: 4.375rem;
  margin: 0 auto;
}
@media screen and (min-width: 768px) {
  .p-footer__logo {
    margin: 0;
  }
}
.p-footer__logo img {
  width: 100%;
}

.p-footer__items {
  display: block;
  width: 100%;
  margin-top: 0.75rem;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .p-footer__items {
    display: inherit;
    justify-content: right;
    align-items: center;
    margin: 0;
  }
}

.p-footer__item {
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
}
@media screen and (min-width: 768px) {
  .p-footer__item {
    padding-left: 2rem;
  }
}

.p-footer__item a {
  letter-spacing: 0.12em;
}
@media screen and (min-width: 768px) {
  .p-footer__item a {
    letter-spacing: 0.2em;
    position: relative;
  }
}
.p-footer__item a::after {
  position: absolute;
  content: "";
  left: 0;
  width: 100%;
  height: 0.125rem;
  background: #F9F871;
  bottom: -0.4375rem;
  transform: scale(0, 1);
  transform-origin: right top;
  transition: transform 0.3s;
}
.p-footer__item a:hover::after {
  transform: scale(1, 1);
  transform-origin: left top;
}

.p-footer__copyright {
  text-align: center;
  padding-top: 1.75rem;
}
@media screen and (min-width: 768px) {
  .p-footer__copyright {
    padding-top: 3.75rem;
  }
}

.p-footer__copyright small {
  font-size: 0.6875rem;
  letter-spacing: 0.12em;
}

.header-logo {
  width: 5rem;
  max-width: 100%;
  padding-top: 0.625rem;
  padding-left: 0.125rem;
  position: relative;
  z-index: 9999;
}
@media screen and (min-width: 768px) {
  .header-logo {
    width: 6.25rem;
    padding-left: 0;
  }
}

.p-header {
  position: fixed;
  width: 100%;
  background-color: rgba(17, 17, 17, 0.5);
  height: 3.125rem;
  z-index: 1000;
}

.p-header__inner {
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1.25rem;
}
@media screen and (min-width: 768px) {
  .p-header__inner {
    padding-left: 2rem;
    padding-right: inherit;
  }
}

@media screen and (min-width: 768px) {
  .p-header__drawer {
    display: none;
  }
}

.p-pc-nav {
  height: inherit;
  display: none;
}
@media screen and (min-width: 768px) {
  .p-pc-nav {
    display: block;
  }
}

.p-pc-nav__items {
  height: inherit;
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  line-height: 3.125rem;
}

.p-pc-nav__item {
  align-items: center;
  height: inherit;
}

.p-pc-nav__item a {
  position: relative;
  height: inherit;
  color: #fff;
  display: grid;
  place-items: center;
  padding: 0 1rem;
  font-size: 1rem;
  letter-spacing: 0.125rem;
}
.p-pc-nav__item a::after {
  position: absolute;
  content: "";
  left: 0.625rem;
  width: 80%;
  height: 0.125rem;
  background: #F9F871;
  bottom: 0.5rem;
  transform: scale(0, 1);
  transform-origin: right top;
  transition: transform 0.3s;
}
.p-pc-nav__item a:hover::after {
  transform: scale(1, 1);
  transform-origin: left top;
}

.p-pc-nav__item.p-pc-nav__item--white a {
  background: #fff;
  color: #333;
  padding: 0 2rem;
  transition: all 0.3s;
}
.p-pc-nav__item.p-pc-nav__item--white a:hover {
  color: #fff;
  background: #333;
}

