/* swiper */
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}
.swiper-wrapper {
  align-items: center;
}

.swiper-slide-active .slide-img,
.swiper-slide-duplicate-active .slide-img,
.swiper-slide-prev .slide-img {
  animation: zoomUp 7s linear 0s 1 normal both;
}

.slide-img {
  display: block;
}

.p-top-lead {
